body {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: Arial, sans-serif;
}


.app {
  padding: 20px;
  max-width: 375px;
  width: 100%;
  min-height: 100vh;
  color: white;
  background: linear-gradient(to bottom, #5b8bdf 0%, #5b8bdf 70%, #03cae6 100%);
  text-shadow: 1px 1px 1px black;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

button {
  width: calc(100% - 40px);
  padding: 10px;
  margin: 20px;
  background-color: #2c64c6;
  color: white;
  font-weight: bold;
  text-shadow: 1px 1px 1px black;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}

h1 {
  margin-bottom: 80px;
  font-size: 3em;
  text-align: center;
}

.image {
  width: calc(100% - 40px);
  height: auto;
  margin: 20px 0;
}

button {
  padding: 10px 20px;
  margin: 20px 0;
}

footer {
  margin-top: auto;
  padding: 10px 0;
  text-align: center;
  text-shadow: none;
}


